.Search{
    position: relative;
}


.SearchBar{
    border:none;
    width: 60%;
    text-align: left;
    padding: 15px;
    height: 40px;
    background-color:#E0E5EC;
    box-shadow: 9px 9px 16px rgb(163,177,198,0.6), -9px -9px 16px    rgba(255,255,255, 0.5);
    border-radius: 25px;
    font-size: 30px;
    text-indent:30px;
    z-index: 100;
}
button{
    background-color:#E0E5EC;
    border:none;
    border-radius: 25px;
    height: 20px;
    outline: none;
    cursor: pointer;
}
.SearchBar{
    outline: none;
}
.placeholder    {
    position: absolute;
    top: 20px;
    left: 590px;
    color: rgb(114, 107, 107);
}
